<template>
  <div class="wrapper">
    <swiper :options="swiperOption" v-if="isShow">
      <swiper-slide v-for="item of swiperList" :key="item.id">
        <img class="swiper-img" :src="item.imgUrl" alt="">
      </swiper-slide>
      <!-- Optional controls -->
      <div class="swiper-pagination" slot="pagination"></div>
      <!-- swiper插件：https://github.com/surmon-china/vue-awesome-swiper -->
      <!-- swiper插件：https://github.com/surmon-china/vue-awesome-swiper/tree/v2.6.7 -->
    </swiper>
  </div>
</template>

<script>
export default {
  name: 'HomeSwiper',
  props: {
    list: Array
  },
  data () {
    return {
      swiperOption: {
        pagination: '.swiper-pagination',
        loop: true,
        autoplay: 3000,
        autoplayDisableOnInteraction: false
      },
      swiperList: []
    }
  },
  watch: { // 监听从Home父组件中传过来的数据并赋值给本组件
    'list': function (val) {
      this.swiperList = val;
    }
  },
  computed: {
    isShow () { // 解决初始swiperList数据为空，在获取到数据后显示到最后一个图的问题
      return this.swiperList.length;
    }
    // swiperList () {
    //   return this.list
    // }
  }
}
</script>

<style lang="stylus" scoped>
.wrapper >>> .swiper-pagination-bullet-active
  background: #fff
.wrapper
  width: 100%
  height: 0
  overflow: hidden
  padding-bottom: 32.7% // (图片高宽比:在图片未被加载出来前占位，仿抖动)
  .swiper-img
    width: 100%
</style>



