<template>
  <div class="icons-wrapper">
    <swiper :options="swiperOption">
      <swiper-slide v-for="(page, index) of pages" :key="index">
        <div class="icon" v-for="item of page" :key="item.id">
          <div class="icon-img-content">
            <img class="icon-img" :src="item.imgUrl" alt="">
          </div>
          <p class="icon-dec">{{item.desc}}</p>
        </div>
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
export default {
  name: 'HomeIcons',
  props: {
    list: Array
  },
  data () {
    return {
      swiperOption: {
        pagination: '.swiper-pagination',
        autoplay: false
      }
    }
  },
  computed: {
    pages () {
      const pages = [];
      this.list.forEach((item, index) => {
        const page = Math.floor(index / 8);
        if (!pages[page]) {
          pages[page] = [];
        }
        pages[page].push(item);
      })
      return pages;
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl'
@import '~styles/mixins.styl'
.icons-wrapper >>> .swiper-container
  width: 100%
  height: 0
  padding-bottom: 56%
.icons-wrapper
  margin-top: 0.1rem
  .icon
    position: relative
    width: 25%
    height: 0
    padding-bottom: 25%
    float: left
    overflow: hidden
    box-sizing: border-box
    .icon-img-content
      position: absolute
      top: 0
      left: 0
      right: 0
      bottom: 0.44rem
      padding: 0.1rem
      .icon-img
        height: 100%
        display: block
        margin: 0 auto
    .icon-dec
      position: absolute
      left: 0
      right: 0
      bottom: 0
      line-height: 0.44rem
      text-align: center
      color: $darkTextCloor
      ellipsis()
</style>



